import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/components/Home.vue';
import TopicList from '@/components/TopicList.vue';
import Topic from '@/components/Topic.vue';
import Login from '@/components/Login.vue';
import Logout from '@/components/Logout.vue';
import Center from '@/components/Center.vue';
import Msg from '@/components/Msg.vue';
import User from '@/components/User.vue';
import Create from '@/components/Create.vue';
import Edit from '@/components/Edit.vue'


// 
Vue.use(VueRouter);

// 
const routes = [
    // { path: '/', name: 'home', component: Home },//Home
    {
        path: '/',
        redirect: '/topiclist/all'
    }, //  topiclist
    {
        path: '/topiclist/:name',
        name: 'topiclist', //:
        component: TopicList,
        props: true
    }, //props props  trueprops
    {
        path: '/topic/:id',
        name: 'topic',
        component: Topic,
        props: true
    },
    // 
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    // 
    {
        path: '/center',
        name: 'center',
        component: Center
    },
    {
        path: '/user/:loginname',
        name: 'user',
        component: User,
        props: true
    },
    // 
    {
        path: '/msg',
        name: 'msg',
        component: Msg
    },
    // 
    {
        path: '/logout',
        name: 'logout',
        component: Logout
    },
      // 
      {
        path: '/create',
        name: 'create',
        component: Create
    },
    //
    {
        path: '/topic/:id/edit',
        name: 'edit',
        component: Edit,
        props: true
    }
];

// 
const router = new VueRouter({
    routes //
});

// 
export default router;



